import title from "../img/title.png";
import '../App.css'
import styles from "./Main.module.css"
import logo from "../img/logo.png"
import arrow from "../img/arrow.png"
import arrow2 from "../img/arrow2.png"
import arrow3 from "../img/arrow3.png"
import gr from "../img/grect.png"
import gr2 from "../img/grect2.png"
import rect from "../img/rect.png"
import rect2 from "../img/rect2.png"
import rect3 from "../img/rect3.png"
import {Link} from "react-router-dom";
import { useState } from "react";

function Main() {
    const [isListHover, setIsListHover] = useState(false);
    return (
        <div id={styles.top}>
            <div id={styles.first}>
                <img src={title} className={styles.title}/>
            </div>
            <div id={styles.second}>
                <div className={styles.intro}   >
                    <span></span>
                    <span>  </span>
                </div>
                <div className={styles.tContent}>
                    <div> , <img src={logo} className={styles.logo}/></div>
                    <div>      </div>
                    <div>    </div>
                    <Link
                        to = '/accodion' 
                        onMouseOver={() => setIsListHover(true)}
                        onMouseOut={() => setIsListHover(false)}
                        className={styles.hText}>
                          ?
                        <img src={isListHover ? arrow2 : arrow} className={styles.arrow}/>
                    </Link>
                    <div>     <br/>
                          .   <br/>
                         .     <br/>
                               <br/>
                          .
                    </div>
                </div>
                <div className={styles.intro} id={styles.gIntro}>
                    <span></span>
                    <span>  </span>
                </div>
                <div className={styles.gContent}>
                    <div id={styles.gTitle}>
                        <div id={styles.gLo}>
                            <img src={logo} className={styles.logo3}/>
                            <div id={styles.gTitle2}><span id={styles.cgr}>  </span>    <br/>
                                     .
                            </div>
                        </div>
                        <div>
                                  .   <br/>
                              , ,      . 
                        </div>
                    </div>
                    <img src={gr} id={styles.gr}/>
                    <div id={styles.gr2}>
                        <img src={gr2}/>
                        <div id={styles.goGame}>
                            <Link to='/game'>
                            <img src={arrow3}/><br/>
                            <br/>
                             
                            </Link>
                        </div>
                    </div>
                </div>
                <div className={styles.intro} id={styles.rIntro}>
                    <span></span>
                    <span>  </span>
                </div>
                <div className={styles.rContent}>
                    
                    <div><span className="nanum">  </span><br/>
                         
                    </div>
                    <div> <span className="nanum"></span></div>
                    <div></div>
                    <div className="nanum"> &nbsp; &nbsp;<br/>
                         
                    </div>
                    <div className="nanum">(?)</div>
                    <div>
                        <div></div>
                        <div></div>
                        <div className="nanum"></div>
                    </div>
                    <div className="nanum">  </div>
                    <div><span></span></div>
                    <div id={styles.thesis}><Link to='/thesis' id={styles.thesis}> </Link></div>
                    <Link to='/thesis'><img src={arrow2} id={styles.arrow2}/></Link>
                    <img src={rect} id={styles.r1}/>
                    <img src={rect2} id={styles.r2}/>
                    <img src={rect3} id={styles.r3}/>
                    <img src={logo} className={styles.logo2}/>
                </div>
            </div>
        </div>
    )
}

export default Main;
