import {Link} from "react-router-dom";

import styles from './Thesis.module.css'
import barrow from './img/bArrow.png';
import bottom from './img/bottom.png'
import arrbtn from './mimg/arrBtn.png'

import water from './mimg/water-1.png'
import water2 from './mimg/water-2.png'
import zoo from './mimg/zoo.png'
import traffic from './mimg/traffic.png'
import world from './mimg/world.png'
import free from './mimg/free-market.png'
import vegetable from './mimg/vegetable.png'
import community from './mimg/community.png'

function Thesis() {

    let goparam = (e) => {
        window.location.href = `/indi/${e}`;
    }

    return (
        <>
            <div id={styles.first}>
                <div>
                    
                    <div id={styles.fText}>&nbsp;&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; 
                        <img src={barrow} id={styles.bArrow}/> 
                          </div>
                    <span className='nanum'>  </span>
                </div>
            </div>
            <div id={styles.second}>
                <img src={bottom}/>
                <div></div>
            </div>   

            <div id={styles.third}>
                <div id={styles.water} onClick={()=> {goparam('')}}>
                    <img src={water}/>
                    <img src={water2} className={styles.wimg}/>
                    <Link to={`/indi/`}><img src={arrbtn} className={styles.arrBtn} id={styles.arrBtn}/></Link>
                    <div className={styles.theText} id={styles.theText}>
                        <div></div>
                          ?
                    </div>
                </div>

                <div id={styles.zoo} onClick={()=> {goparam('')}}>
                    <img src={zoo}/>
                    <Link to={`/indi/`}><img src={arrbtn} className={styles.arrBtn} id={styles.arrBtn2}/></Link>
                    <div className={styles.theText} id={styles.theText2}>
                        <div></div>
                          ?
                    </div>
                </div>

                <div id={styles.traffic} onClick={()=> {goparam('')}}>
                    <img src={traffic}/>
                    <Link to={`/indi/`}><img src={arrbtn} className={styles.arrBtn} id={styles.arrBtn3}/></Link>
                    <div className={styles.theText} id={styles.theText2}>
                        <div></div>
                          ,  ?
                    </div>
                </div>

                <div id={styles.world} onClick={()=> {window.location.href = `/record`;}}>
                    <img src={world}/>
                    <Link to={`/record`}><img src={arrbtn} className={styles.arrBtn} id={styles.arrBtn4}/></Link>
                    <div className={styles.theText} id={styles.theText3}>
                        <div></div>
                          <br/>
                         ?
                    </div>
                </div>

                <div id={styles.free} onClick={()=> {goparam('')}}>
                    <div className={styles.theText} id={styles.theText4}>
                        <div></div>
                          
                    </div>
                    <img src={free}/>
                    <Link to={`/indi/`}><img src={arrbtn} className={styles.arrBtn} id={styles.arrBtn5}/></Link>
                </div>

                <div id={styles.vegetable} onClick={()=> {goparam('')}}>
                    <div className={styles.theText} id={styles.theText5}>
                        <div></div>
                           <br/>
                          
                    </div>
                    <img src={vegetable}/>
                    <Link to='/indi/'><img src={arrbtn} className={styles.arrBtn} id={styles.arrBtn6}/></Link>
                </div>

                <div id={styles.community} onClick={()=> {goparam('')}}>
                    <img src={community}/>
                    <Link to={`/indi/`}><img src={arrbtn} className={styles.arrBtn} id={styles.arrBtn7}/></Link>
                    <div className={styles.theText} id={styles.theText6}>
                        <div></div>
                           <br/>  ?
                    </div>
                </div>
             </div>   
        </>
    )
}

  

  

export default Thesis;